import React from "react";
import { useSelector } from "react-redux";
import { MDBAnimation, MDBContainer } from "mdbreact";
import ProductGroups from "./ProductGroups";
import Trilha from "../Trilha";
import Carrinho from "../carrinho";

function CompreAqui() {
  let produtos = useSelector(state => state.products.produtos);

  return (
    <MDBAnimation type="slideInLeft">
      <Trilha pagename="Compre aqui" />
      <h5>Monte o carnaval do seu jeito</h5>
      <ProductGroups categoria={16} titulo={"CAMAROTES"} produtos={produtos} />
      <ProductGroups categoria={17} titulo={"BLOCOS"} produtos={produtos} />
      <MDBContainer className="fixed-bottom">
        <Carrinho />
      </MDBContainer>
    </MDBAnimation>
  );
}

export default CompreAqui;
